.cs-email-tags-filter {
  margin: -15px -15px 15px;
  padding: 15px;
  border-bottom: 1px solid #ddd;
  background: #f3f3f3; }

.cs-email-tags-filter-search {
  padding: 10px;
  width: 100%;
  font-size: 20px; }

.cs-email-tags-list {
  margin: 0; }

.cs-email-tags-list-item {
  margin-bottom: 15px; }

.cs-email-tags-list-item::last-child {
  margin-bottom: 0; }

.cs-email-tags-list-button {
  cursor: pointer;
  color: #666;
  text-align: left;
  padding: 0.8rem;
  width: 100%;
  background: none;
  border: 1px solid #ddd;
  border-radius: 3px; }

.cs-email-tags-list-button:hover {
  background: #fafafa; }

.cs-email-tags-list-button strong {
  color: #444;
  font-size: 14px;
  margin-bottom: 8px; }

.cs-email-tags-list-button code {
  margin-left: 10px; }

.cs-email-tags-list-button span {
  display: block;
  margin-top: 10px; }
